export const ROLE_SUMMARY_PERMISSION_COLUMN = [
	{
		Header: '',
		accessor: 'name',
	},
	{
		Header: ' ',
		accessor: 'description',
		Cell: function Component(v) {
			return (
				<div>
					{v.value.split('\n').map((v, i) => {
						return (
							<RowDiv
								alignItems={'center'}
								height={'30px'}
								key={i}
							>
								{v}
							</RowDiv>
						);
					})}
				</div>
			);
		},
	},
	{
		Header: '',
		accessor: 'type',
	},
	{
		Header: ' ',
		accessor: 'policyName',
	},
	{
		accessor: 'creationDate',
		Header: ' ',
	},
	{
		accessor: 'grantUser',
		Header: ' ',
		Cell: function Component(v) {
			return <div>{v.value?.name + '(' + v.value?.id + ')'}</div>;
		},
	},
];
