import React, {useRef} from 'react';
import PropTypes from 'prop-types';
import Form from '../../RecycleComponents/New/Form';
import TextBox from '../../RecycleComponents/New/TextBox';
import {searchIcon} from '../../../icons/icons';
import {Icon} from '../../../styles/components/icons';
import {tableKeys} from '../../../Constants/Table/keys';

const placeholders = {
	[tableKeys.users.basic]: ' ,  , ',
	[tableKeys.users.add.groups.exclude]: ' ',
	[tableKeys.users.add.roles.exclude]: ' ',
	[tableKeys.users.summary.tabs.groups.include]: ',  ',
	[tableKeys.users.summary.tabs.groups.exclude]: ',  ',
	[tableKeys.users.summary.tabs.roles.include]: ' , ',
	[tableKeys.users.summary.tabs.roles.exclude]: ' , ',
	[tableKeys.users.summary.tabs.tags.permissions.include]: ' , ',
	[tableKeys.users.summary.tabs.tags.permissions.exclude]: ' , ',
	[tableKeys.groups.basic]: ' ,   ',
	[tableKeys.groups.add.users.exclude]: ' ,  ',
	[tableKeys.groups.add.roles.exclude]: ' , ',
	[tableKeys.groups.summary.tabs.users.include]: ' ,  ',
	[tableKeys.groups.summary.tabs.users.exclude]: ' ,  ',
	[tableKeys.groups.summary.tabs.roles.include]: ' , ',
	[tableKeys.groups.summary.tabs.roles.exclude]: ' , ',
	[tableKeys.groups.summary.tabs.tags.permissions.include]: ' , ',
	[tableKeys.groups.summary.tabs.tags.permissions.exclude]: ' , ',
	[tableKeys.roles.basic]: ' ,   ',
	[tableKeys.roles.summary.tabs.permissions.include]: ' , ',
	[tableKeys.roles.summary.tabs.permissions.exclude]: ' , ',
	[tableKeys.roles.summary.tabs.users.include]: ' ,  ',
	[tableKeys.roles.summary.tabs.users.exclude]: ' ,  ',
	[tableKeys.roles.summary.tabs.groups.include]: ' ,  ',
	[tableKeys.roles.summary.tabs.groups.exclude]: ' ,  ',
};

function Search({tableKey, setGlobalFilter}) {
	const ref = useRef(null);
	const handleSubmit = (data) => {
		console.log('api');
		console.log('tableKey', tableKey);
		console.log('data', data);
		setGlobalFilter(data['search']);
	};

	return (
		<Form
			onSubmit={handleSubmit}
			innerRef={ref}
			initialValues={{search: ''}}
		>
			<TextBox
				placeholder={placeholders[tableKey]}
				front={
					<Icon size={'sm'} margin_right={'0px'}>
						{searchIcon}
					</Icon>
				}
				name={'search'}
			/>
		</Form>
	);
}
Search.propTypes = {
	tableKey: PropTypes.string,
	setGlobalFilter: PropTypes.func,
};
export default Search;
