import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const USERS_GROUPS = ['       .'];
const USERS_ROLES = ['       .'];
const USERS_INFO = [
	'         .',
];
const GROUPS = [
	'  1   .',
	' 10  .',
];
const GROUPS_TYPE = [
	'  100  .',
	'    300   .',
];
const ROLES = [
	'Private     .',
	' 10  .',
];
const POLIICIES = [
	'        .',
	' 10   .',
];
const TAGS = [
	'IAM      - .',
	'           .',
	'key()  100, ()  250  .',
	'   10  .',
];

const _TableOptionTextContainer = styled.div`
	padding: 0px 17px 10px 17px;
	font-size: 14px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.6;
	letter-spacing: 0.1px;
	color: #212121;
`;

const _TableOptionTextContants = styled.div`
	padding: 2px 2px;
	display: flex;
	align-items: center;
	font-size: 12px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.6;
	letter-spacing: 0.1px;
	text-align: left;
	color: #959ea1;
`;

const TableOptionText = ({data}) => {
	const optionText = (data) => {
		switch (data) {
			case 'usersGroups':
				return USERS_GROUPS;
			case 'usersRoles':
				return USERS_ROLES;
			case 'usersInfo':
				return USERS_INFO;
			case 'groups':
				return GROUPS;
			case 'groupsType':
				return GROUPS_TYPE;
			case 'roles':
				return ROLES;
			case 'policies':
				return POLIICIES;
			case 'tags':
				return TAGS;
			default:
				return '';
		}
	};
	return (
		<_TableOptionTextContainer>
			{optionText(data).map((v, index) => {
				return (
					<div key={index}>
						<_TableOptionTextContants key={index}>
							<li style={{paddingLeft: '5px'}}> {v}</li>
						</_TableOptionTextContants>
					</div>
				);
			})}
		</_TableOptionTextContainer>
	);
};
TableOptionText.propTypes = {
	data: PropTypes.string.isRequired,
};
export default TableOptionText;
