import React, {useCallback, useRef} from 'react';
import {useHistory} from 'react-router-dom';
import {
	AppBarButtons,
	InputDescriptionText,
	SubHeader,
	SubHeaderText,
} from '../../../styles/components/style';
import * as yup from 'yup';
import CURRENT_TARGET from '../../../reducers/currentTarget';
import {useDispatch} from 'react-redux';
import PropTypes from 'prop-types';
import {
	NormalButton,
	TransparentButton,
} from '../../../styles/components/buttons';
import Form from '../../RecycleComponents/New/Form';
import TextBox from '../../RecycleComponents/New/TextBox';
import {AddPageContentsContainer, RowDiv} from '../../../styles/components/div';
import TableOptionText from '../../Table/Options/TableOptionText';

const AddUser = ({setIsOpened}) => {
	const history = useHistory();
	const formRef = useRef(null);

	const dispatch = useDispatch();
	//    .
	const telephoneRegex = /^\d{2,3}-\d{3,4}-\d{4}$/;
	const mobileRegex = /^[0-9]{3}[-]+[0-9]{4}[-]+[0-9]{4}$/;

	const validation = {
		id: yup
			.string()
			.min(10, '  10 .')
			.max(40, '  40 .')
			.required('   .'),
		name: yup
			.string()
			.max(30, '  30 .')
			.required('   .'),
		email: yup
			.string()
			.email('   .')
			.required('   .'),
		telephone: yup
			.string()
			.matches(telephoneRegex, '  .'),
		mobile: yup
			.string()
			.required('    .')
			.matches(mobileRegex, '  .'),
	};

	const onClickCancelAddUser = useCallback(() => {
		history.push('/users');
	}, [history]);

	const onSubmitUserData = useCallback(
		(data) => {
			dispatch(
				CURRENT_TARGET.action.addReadOnlyData({
					title: 'user',
					data: data,
				}),
			);
			setIsOpened(true);
		},
		[dispatch, setIsOpened],
	);

	return (
		<>
			<SubHeader>
				<SubHeaderText>  </SubHeaderText>
				<AppBarButtons>
					<NormalButton
						onClick={() => formRef.current.handleSubmit()}
					>
						 
					</NormalButton>
					<TransparentButton
						margin='0px 0px 0px 5px'
						onClick={onClickCancelAddUser}
					>
						
					</TransparentButton>
				</AppBarButtons>
			</SubHeader>
			<TableOptionText data={'usersInfo'} />
			<AddPageContentsContainer>
				<Form
					initialValues={{
						id: '',
						name: '',
						email: '',
						telephone: '',
						mobile: '',
					}}
					onSubmit={onSubmitUserData}
					innerRef={formRef}
					validation={validation}
				>
					<RowDiv margin={'0px 0px 12px 0px'}>
						<TextBox
							name={'id'}
							placeholder={'  ID'}
							direction={'row'}
						/>
						<InputDescriptionText>
							 40,    .
						</InputDescriptionText>
					</RowDiv>
					<RowDiv margin={'0px 0px 12px 0px'}>
						<TextBox
							name={'name'}
							placeholder={' '}
							direction={'row'}
						/>
						<InputDescriptionText>
							 30,   .
						</InputDescriptionText>
					</RowDiv>
					<RowDiv margin={'0px 0px 12px 0px'}>
						<TextBox
							name={'email'}
							placeholder={' '}
							direction={'row'}
						/>
						<InputDescriptionText>
							 200 .
						</InputDescriptionText>
					</RowDiv>
					<RowDiv margin={'0px 0px 12px 0px'}>
						<TextBox
							name={'telephone'}
							placeholder={''}
							direction={'row'}
						/>
						<InputDescriptionText>
							+82-(0)70-4469-4469  .
						</InputDescriptionText>
					</RowDiv>
					<RowDiv margin={'0px 0px 12px 0px'}>
						<TextBox
							name={'mobile'}
							placeholder={' '}
							direction={'row'}
						/>
						<InputDescriptionText>
							+82-(0)70-4469-4469  .
						</InputDescriptionText>
					</RowDiv>
				</Form>
			</AddPageContentsContainer>
		</>
	);
};

AddUser.propTypes = {
	setIsOpened: PropTypes.func,
};

export default AddUser;
